import React from "react";
import { StyledCountdown } from "./StyledCountdown";

export interface dateCountdown {
  date: string;
}
function Countdown({ date }: dateCountdown) {
  const countDownDate = new Date(date).getTime();
  var x = setInterval(function () {
    // Get today's date and time
    var now = new Date().getTime();

    // Find the distance between now and the count down date
    var distance = countDownDate - now;

    // Time calculations for days, hours, minutes and seconds
    var days = Math.floor(distance / (1000 * 60 * 60 * 24));
    var hours = Math.floor(
      (distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)
    );
    var minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
    var seconds = Math.floor((distance % (1000 * 60)) / 1000);

    // Display the result in the element with id="demo"
    document.getElementById(
      "countdown"
    )!.innerHTML = `<div className="days">${days}<p>days</p></div> 
    <div className="hours">${hours}<p>hours</p></div> 
    <div className="minutes">${minutes}<p>minutes</p></div> 
    <div className="seconds">${seconds}<p>seconds</p></div>`;

    // If the count down is finished, write some text
    if (distance < 0) {
      clearInterval(x);
      document.getElementById("countdown")!.innerHTML = "EXPIRED";
    }
  }, 1000);

  return (
    <StyledCountdown>
      <p>Promo available for:</p>
      <div id="countdown"></div>
    </StyledCountdown>
  );
}

export default Countdown;
